<template>
	<view class='wrapper'>
		<view class="user_name flex-row-start-end">
			<text class="member_name">{{clientOrder.memberName}}</text>
			<text class="staff_name text-blue" @click="telPhone(clientOrder.staffMobile)">推荐员工：{{clientOrder.staffName}}({{clientOrder.staffMobile}})</text>
		</view>
		<view class="switch">
			<scroll-view
				:scroll-x="true" 
				:scroll-with-animation="true" 
				:enable-back-to-top="true"
				:scroll-into-view="domIndex"
			>
			<view class='list' :style="[{width:parentWidth *130+'px'}]">
				<view 
					:class="[idx==index?'active':'','item']" 
					v-for="(item,index) in clientOrder.orderList" 
					:key="index" 
					@click="changeSlide(item,index)"
					:id="'dom'+index"
				>
					<view class="want">意向车型</view>
					<view class="modal">{{item.carTypeName}}</view>
				</view>
			</view>
			</scroll-view>
		</view>
		<view class="cu-timeline">
			<view 
				:class="['cu-item',currentStep>=item.step?'text-golden':'no_pass',currentStep==item.step?'active':'']"
				 v-for="(item,index) in progressList"
				:key="index"
			 >
				<view class="look_car time_item">
					<view class="title">{{item.name}}</view>
					<!-- 普通节点 -->
					<block v-if="item.step<6">
						<view class="select_date" v-if="currentStep>=item.step">{{item.nodeDate}}</view>
					</block>
					<!-- 上传附件节点 -->
					<block v-else>
						<view class="select_date" v-if="currentStep>=item.step">{{item.nodeDate}}</view>
						<view class="upload_w" v-if='currentStep>=item.step'>
							<view class="upload">
								<view class="item" :key='idx' v-for="(lett,idx) in affixList">
									<view @click='lookAffix(lett,idx)' class='inner_w'>
										<img :src="lett" class='pic'>
									</view>
								</view>
							</view>
						</view>
					</block>		
				</view>
			</view>
			<!-- 订单项目组审核才能有结果 -->
			<view :class="['cu-item',currentStep>=totalStep && currentOrderInfo.orderState>=20?'text-golden':'no_pass',currentStep==totalStep?'active':'']">
				<view class="look_car time_item">
					<view class="title">发放奖励</view>
					<view class="select_date" v-if="currentStep>=totalStep && currentOrderInfo.orderState>=20">{{currentOrderInfo.awardDelivered==1?"发放":"不发放"}}</view>
				</view>
			</view>
		</view>
		
		<!--  -->
		<view class="cancel_order"
			v-if="currentOrderInfo.orderState!=-1 && currentOrderInfo.orderState<15"
			@click="cancleOrder"
		>
			<button class="cu-btn">
				取消订单
			</button>
		</view>
		
		
		<!-- 图片查看 -->
		<view class="cu-modal" :class="lookImg?'show':''" catchtouchmove="return">
			<view class="cu-dialog">
				<view class="bg-img">
					<image :src="currentImgUrl" class="dialogImg" mode="widthFix"></image>
				</view>
				<view class="cu-bar bg-white">
					<view class="action margin-0 flex-sub  solid-left" @click="hideModal">关闭</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import request from "../../common/request.js"
	import {http2Https,getFileType,getDeleteId,formatTime,https2Http} from '../../common/tools.js'
	export default {
		data() {
			return {
				idx:0,
				domIndex:'dom0',
				currentStep:0, //步数
				totalStep:0,
				currentOrderId:"",
				progressList:[],// 节点进度
				clientOrder:{
					memberName:'',
					orderList:[],
				},
				affixList:[],
				parentWidth:0,
				lookImg:false,
				currentImgUrl:null,
				currentOrderInfo:{}, //当前订单
			};
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
		methods:{
			scroll(e){
				console.log(e)
			},
			hideModal(){
				this.lookImg=false
			},
			changeSlide(item,index){
				this.idx=index
				this.affixList=[]
				this.getOrderProgress(item.orderId)
			},
			
			lookAffix(item,idx){
				if(this.mobileModel=="VOG-AL00"){ //华为p30
					this.currentImgUrl=item;
					this.lookImg=true
				}else{
					uni.showLoading()
					uni.previewImage({
						current:item,
						urls:this.affixList,
						longPressActions:{
							itemList: ['发送给朋友', '保存图片', '收藏']
						},
						fail(){
							this.currentImgUrl=item;
							this.lookImg=true
						},
						complete() {
							uni.hideLoading()
						}
					})
				}
			},
			telPhone(tel){
				uni.showModal({
					title:"提示",
					content:"是否确认拨打电话",
					success:(res)=>{
						if(res.confirm){
							uni.makePhoneCall({
							  phoneNumber:tel //仅为示例，并非真实的电话号码
							})
						}
					}
				})
			},
			cancleOrder(){
				uni.showModal({
					title: '提示',
					content: `确认取消该订单吗？`,
					success: (res)=>{
						if (res.confirm) {
							this.request("/order/admin/cancel",{orderId:this.currentOrderId}).then((res)=>{
								if(res.resultCode==0){
									uni.showToast({
										title:res.resultMsg,
										icon:"none",
										success:()=>{
											setTimeout(()=>{
												uni.navigateBack({
													delta:1
												})
											},1500)
										}
									})
								}else{
									uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
								}
							})
						}
					}
				})
				
			},
			getPicList(){
				if(this.currentOrderInfo.memberAttUrls){
					let _imgList=this.currentOrderInfo.memberAttUrls.split(',')
					this.affixList=[..._imgList]
				}
				if(this.currentOrderInfo.staffAttUrls){
					let _imgList=this.currentOrderInfo.staffAttUrls.split(',')
					this.affixList=this.affixList.concat(_imgList)
				}
			},
			getOrderProgress(orderId){
				this.request("/order/progress/show",{orderId:orderId}).then((res)=>{
					if(res.resultCode==0){
						let step=0
						// 判断节点处于第一步
						this.progressList=res.data
						this.totalStep=res.data.length //6
						this.progressList.forEach((item,index)=>{
							item.step=index+1; // 赋予步数
							if(item.nodeDate!=-1){
								item.nodeDate=formatTime(item.nodeDate,true)
								step++
							}
						})
						this.getPicList()
						this.currentStep=step //6
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			}
		},
		onLoad(params){
			
			/**
			 * 接受两个参数 
			 * 
			 * currentOrderId
			 * 
			 * clientOrder 订单信息
			 * 
			 */
			this.clientOrder=JSON.parse(params.clientOrder) //当前用户所有订单信息
			if(params.currentOrderId){
				this.currentOrderId=params.currentOrderId //员工查看
			}else{
				this.currentOrderId=this.clientOrder.orderList[0].orderId //管理元查看
			}
			//遍历当前索引
			this.clientOrder.orderList.forEach((item,index)=>{
				if(item.orderId==this.currentOrderId){
					this.idx=index
					this.domIndex="dom"+this.idx
					this.currentOrderInfo=this.clientOrder.orderList[index]
					console.log(this.currentOrderInfo)
				}
			})
			// 获取当前进度
			this.getOrderProgress(this.currentOrderId)
			this.parentWidth=this.clientOrder.orderList.length
		}
	}
</script>

